/* eslint-disable @typescript-eslint/no-explicit-any */
import { useCallback } from 'react';
type HookCallback<T extends any> = (...args: T[]) => any;
export type Hook = (cb: HookCallback<any>, ...args: any[]) => any;
export function makeUseRefTarget<T extends Hook,
  C = T extends (cb: infer U) => unknown ? U : never,
  B = C extends (...args: any[]) => infer U ? U : void,
  A = T extends (cb: C, ...args: (infer U)[]) => unknown ? U : Parameters<T>,
  D = C extends (...args: infer U) => any ? U : never>(hook: T) {
  return <R extends React.MutableRefObject<any>,
    RC = R extends React.MutableRefObject<infer U> ? NonNullable<U> : never>(
    cb: (refs: RC[], args: D) => (B | void),
    refs: R[],
    ...args: A[]) => {
    const cCb = useCallback((...cbArgs) => {
      const curRefs = refs.map(r => r.current);
      if (curRefs.every(r => typeof r !== 'undefined'))
        return cb(curRefs, cbArgs as unknown as D);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [...refs, cb]);
    return hook(cCb, ...args);
  };
}
