import { PointLightProps, useFrame } from '@react-three/fiber';
import { useRef } from 'react';
import { useSpring, animated } from '@react-spring/three';
import { Vector3Array } from '@anonymous.club/types';

type CirculatingLightProps = {
  shouldStop?: boolean;
  center: Vector3Array;
  axis: Vector3Array;
  radius: number;
  offset?: number;
  period?: number;
  freq: number;
} & PointLightProps;
const TICK_FORWARD = 1 / 30;
const TICK_BACKWARD = -(1 / 180);
export function CirculatingLight({
  shouldStop = false,
  center: [cX, cY, cZ] = [0, 0, 0],
  axis: [aX, aY, aZ] = [1, 0, 1],
  radius = 100,
  period = 30,
  freq = 1 / period,
  offset = 0,
  ...props
}: CirculatingLightProps) {
  const ref = useRef();
  const { sR, time } = useSpring({ sR: radius, time: offset, config: { tension: 20, friction: 190, } });
  const [position, api] = useSpring(() => ({
    x: cX + radius * aX,
    y: cY + radius * aY,
    z: cZ + radius * aZ
  }));
  useFrame(({ clock }) => {
    const tD = time.get();
    if (shouldStop) {
      // sR.start(radius * 2);
      time.start(tD + TICK_BACKWARD, {immediate: true});
    } else {
      time.set(tD + TICK_FORWARD);
    }
    const r = sR.get();
    const t = offset + tD * freq;
    api.start({
      z: cZ + Math.sin(t) * r * aZ,
      x: cX + Math.cos(t) * r * aX,
      y: cY + Math.sin(t) * r * aY,
    });
  });
  return (
    <animated.pointLight
      castShadow
      intensity={4}
      shadow-bias={-0.00002}
      shadow-mapSize-height={2048}
      shadow-mapSize-width={2048}
      {...props}
      position-x={position.x}
      position-y={position.y}
      position-z={position.z}
      ref={ref}
      />
  );
}
