import { Vector3 } from 'three';

export const MAX_CAMERA_DISTANCE = 4000;
export const INITIAL_CAMERA_POSITION = new Vector3(-50, -100, MAX_CAMERA_DISTANCE / 2);

export const INITIAL_CAMERA_POSITION_LENGTH_SQ = INITIAL_CAMERA_POSITION.lengthSq();
export const INITIAL_CAMERA_POSITION_LENGTH = INITIAL_CAMERA_POSITION.length();
