import { Vector3 } from 'three';
import create from 'zustand';
import { WorldState } from '@anonymous.club/types';


export const useWorldStore = create<WorldState>((set, get) => ({
  isHeroHovered: false,
  shouldShowHelpers: true,
  shouldShowBottom: false,
  heroCenter: new Vector3(0, 0, 0),
  onHeroHover({ down: isHeroHovered }) {
    set({ isHeroHovered });
  },
  setHeroCenter(model) {
    const heroCenter = new Vector3();
    console.log(model.getWorldPosition(heroCenter));
    console.log(heroCenter);
    set({ heroCenter });
  },
  setBottomOpen(shouldShowBottom) {
    set({shouldShowBottom});
  }
}));
