function WorldBackDrop() {
  const ref = useRef<Mesh>();
  const buffer = useBufferHeight();
  const points = useMemo(() => {
    const bigR = buffer.max;
    return new EllipseCurve(1, 0, bigR, bigR, 0.5 * Math.PI, 1.5 * Math.PI, true, 0).getSpacedPoints(64);
  }, [buffer.max]);
  useRefFrame(([mesh], [{camera}]) => {
    mesh.rotation.copy(camera.rotation);
  }, [ref]);
  return (
    <mesh
      // castShadow
      receiveShadow
      layers={0}
      ref={ref}
      scale={[1.01, 1.01, 1.01]}
      >
      {/* <sphereGeometry args={[150, 64, 64]} /> */}
      <latheGeometry args={[points, 64, Math.PI * 0.5, Math.PI * 1.0]} />
      {/* <shadowMaterial color={0xffffff} /> */}
      <meshStandardMaterial

        // wireframe
        attach="material"
        // clearcoat={1}
        // clearcoatRoughness={0.99}
        // color={0xffffff}
        // color={0xababab}
        depthTest={false}
        fog={false}
        metalness={0.5}
        // roughness={0.99}
        // side={DoubleSide}
        >
        {/* <canvasTexture
          args={[domElement]}
          attachObject={["attributes", "envMap"]}
          ref={cRef}
          /> */}
      </meshStandardMaterial>
    </mesh>
  );
}

const circleSlice = (arr: any[], count: number) => (idx: number) => {
  let i = idx;
  const out = [];
  while (out.length < count) {
    i %= arr.length;
    out.push(arr[i]);
    i++;
  }
  return out;
};

